.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.movie-container{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap:20px;
}
.movie-wrapper{
  width: 500px;
  background-color:antiquewhite;
  color: black;
}
.movie-search{
  height: 48px;
  font-size:24px;
  margin-bottom:32px;
  padding:16px;
  font-weight:bold;
  text-align: center;
  width:800px;
}

.movie-tittle{
  height: 60px;
  margin-top: 26px;
  font-weight: bold;
}

.movie-date{
  margin-top: 26px;
  font-style: italic;
}

.movie-rate{
  color: red;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 1,2em;
}


@media only screen and (max-width: 412px) {
  .movie-wrapper{
    width: 300px;
    height: 400px;
    background-color:antiquewhite;
    color: black;
  }
  .movie-image{
    height: 230px;
  }
  .movie-search {
      width:300px;
  }
}
@media only screen and (max-width: 768px )and (min-width: 420px) {
  .movie-search{
    width: 475px;
  }
}